@import url(https://fonts.googleapis.com/css?family=Tangerine:bold,bolditalic|Inconsolata:italic|Droid+Sans:wght@100&display=swap);
:root {
  --bg-button: #f0f0f0;
  --border-button: solid 1px #888;
}

.button {
  font-size: 1.4em;
  background-color: #f0f0f0;
  background-color: var(--bg-button);
  border: none;
  border-right: solid 1px #888;
  border-right: var(--border-button);
  border-bottom: solid 1px #888;
  border-bottom: var(--border-button);
  outline: none;
}

.button:active {
  background-color: #ccc;
}

.button.double {
  grid-column: span 2;
}

.button.triple {
  grid-column: span 3;
}

.button.operation {
  background-color: #fa8231;
  color: #fff;
}

.button.operation:active {
  background-color: #fa8231cc;
}
.display {
  grid-column: span 4;
  background-color: #0004;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
  font-size: 2.1em;
  overflow: hidden;
}
.calculator {
  height: 320px;
  width: 235px;
  border-radius: 5px;
  overflow: hidden;

  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: 1fr repeat(5, 48px);
}

* {
  font-family: "Tangerine", monospace;
}

body {
  display: -webkit-flex;
  display: flex;
  height: 50vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;

  color: rgb(13, 241, 32);
  background: linear-gradient(to right, rgb(27, 151, 223), rgb(41, 89, 73));
}

